body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 1rem;
  background-color: #01080d;
  color: #fffff7;
  font-weight: 300;
}

button {
  padding: 0.8rem;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 0.8rem;
  background-color: #01080d;
  color: #fffff7;
  border-radius: 8px;
  cursor: pointer;
  text-transform: uppercase;
  border: 1px solid #fffff7;
}
button:hover {
  background: #03131f;
}
button:active {
  transform: scale(0.9);
  box-shadow: inset 0 0 10px #f7f7f7;
}
button:focus {
  outline: none;
}
h1 {
  font-weight: 400;
}
h3,
h4 {
  margin-bottom: 0;
  color: #fffff7;
  font-weight: 300;
}

nav {
  border-bottom: 4px solid #fffff7;
  text-align: center;
  position: fixed;
  top: 0;
  width: 100%;
  background: #01080d;
  color: #fffff7;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav li {
  display: inline-block;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

nav a {
  text-decoration: none;
  font-weight: 300;
  padding: 1rem 32px;
  color: #fffff7;
}

nav a:hover {
  background: #fffff7;
  color: black;
}

#page-body {
  max-width: 700px;
  margin: auto;
  padding-top: 64px;
}

.article-list-item {
  text-decoration: none;
  color: #fffff7;
  padding: 1rem 0 0 0;
}

.article-list-item p {
  padding-bottom: 32px;
  border-bottom: 2px solid #fffff7;
  text-align: justify;
}

#add-comment-form {
  border-bottom: 2px solid #fffff7;
  padding: 1rem;
}

#add-comment-form h3 {
  margin-bottom: 1rem;
}

#add-comment-form input,
textarea {
  display: block;
  width: 300px;
  padding: 8px;
  border-radius: 8px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  border: 2px solid #fffff7;
  margin: 1rem 0;
}

#add-comment-form button {
  width: 320px;
}
#add-comment-form input:focus,
textarea:focus {
  outline: none;
}

#upvotes-section button,
p {
  display: inline-block;
  margin-right: 1rem;
}

.comment {
  border-bottom: 2px solid #fffff7;
  padding: 1rem;
}
